@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0%;
  margin: 0;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

body {
  font-family: "Rubik", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: #fff;
  margin: 0 8%;
}

a {
  color: lightgray;
}

.App{text-align:left;padding:0px 20px 20px 20px}.won-card{background-color:#418a63}.lost-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;margin-bottom:12px;padding:12px 12px 0 12px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue{margin-top:5px}.MatchDetailCard .match-result{margin-top:16px}.MatchDetailCard .additional-match-details{text-align:right}.MatchDetailCard .additional-match-details>p{margin-bottom:20px}

.MatchSmallCard{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.MatchSmallCard .vs{font-size:20px}.MatchSmallCard .other-team-name{margin-top:8px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .go-to-home-page{position:absolute;margin-top:-150px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end;margin-bottom:40px}.TeamPage .team-name-section h1{font-size:4rem}.TeamPage .win-loss-section{display:-webkit-flex;display:flex;-webkit-justify-content:right;justify-content:right;-webkit-align-items:center;align-items:center}.TeamPage .win-lose-pie-chart{width:66%;height:66%;margin-left:auto}.TeamPage .latest-match-section{grid-column:span 4}.TeamPage .recent-matches{padding:12px}.TeamPage .more-button{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.TeamPage .more-button>a{color:#fff;font-size:20px;margin-left:20px}

.YearSelector .year-selector-list{list-style:none;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.YearSelector .year-selector-list li{margin-bottom:16px;text-align:right;margin-right:22px}.YearSelector .active{font-weight:bold;font-size:20px}

.MatchPage{display:grid;grid-template-columns:2fr 10fr;margin-top:26px}.MatchPage .go-back-link{text-align:right;color:#fff}.MatchPage .go-back-link:hover{color:lightgray}.MatchPage .team-match-head{text-align:center;margin-top:10px;margin-bottom:20px}.MatchPage .match-info{grid-column:span 1}.MatchPage .no-matches-found{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}

.HomePageTeam{height:250px;width:auto;border:2px solid royalblue;margin:10px 5px 5px 5px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background-color:#207398;border-radius:16px}.HomePageTeam:hover{background-color:#368cb1;text-decoration:none}.HomePageTeam h1{text-align:center}

.IndexPage{display:grid;grid-template-columns:1fr 1fr}.IndexPage .ipl-db-head{margin-top:40px;margin-bottom:20px;grid-column:span 2;text-align:center}

